    }
} */
.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 87.4375em) {
  .container {
    max-width: 69.375rem;
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.toggle_button {
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  background-color: var(--toggle);
}